.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.danger{
  background-color: rgba(239, 6, 0, 0.1); /*Same color as the left border with reduced alpha to 0.1*/
  width:53%;
  background-color: #ff000080;
  padding:20px;
  border:1px solid #eee;
  border-radius: 3px;
  font-family: 'Open Sans', sans-serif;
  font-size: 16px;
  margin: 0 auto;
}

.danger strong{
  color: #ff0600;
  font-weight: 900;
}



@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
